/* ========================================
   GOANFLOW - MODERN DARK UI
   BeachHack Season 7 Inspired Design
   ======================================== */

/* ========================================
   APP SHELL & LAYOUT
   ======================================== */

.app-shell {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background: var(--bg-primary);
}

/* ========================================
   HEADER - Bold Modern Style
   ======================================== */

.app-header {
  height: 72px;
  background: var(--bg-secondary);
  border-bottom: 1px solid var(--border-light);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 var(--space-8);
  position: sticky;
  top: 0;
  z-index: 100;
  backdrop-filter: blur(12px);
}

.logo {
  font-family: var(--font-display);
  font-size: 1.5rem;
  font-weight: 800;
  color: var(--text-primary);
  display: flex;
  align-items: center;
  gap: var(--space-3);
  letter-spacing: -0.03em;
}

.logo span {
  color: var(--accent-500);
}

.header-nav {
  display: flex;
  gap: var(--space-1);
  background: var(--bg-tertiary);
  padding: var(--space-1);
  border-radius: var(--radius-full);
  border: 1px solid var(--border-light);
}

.header-nav button {
  background: transparent;
  border: none;
  font-size: 0.875rem;
  font-weight: 600;
  color: var(--text-secondary);
  cursor: pointer;
  padding: var(--space-2) var(--space-5);
  border-radius: var(--radius-full);
  transition: all var(--transition-fast);
  font-family: var(--font-display);
}

.header-nav button:hover {
  color: var(--text-primary);
  background: var(--bg-elevated);
}

.header-nav button.active {
  color: var(--text-inverse);
  background: var(--accent-500);
  box-shadow: var(--shadow-glow);
}

.header-right {
  display: flex;
  gap: var(--space-3);
  align-items: center;
}

.theme-toggle,
.menu-toggle {
  background: var(--bg-tertiary);
  border: 1px solid var(--border-light);
  width: 44px;
  height: 44px;
  border-radius: var(--radius-lg);
  font-size: 1.2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all var(--transition-fast);
  color: var(--text-secondary);
}

.theme-toggle:hover,
.menu-toggle:hover {
  background: var(--primary-500);
  border-color: var(--primary-500);
  color: var(--text-primary);
  transform: translateY(-2px);
  box-shadow: var(--shadow-blue-glow);
}

/* ========================================
   MAIN CONTENT AREA
   ======================================== */

.app-main {
  display: flex;
  flex: 1;
  overflow: hidden;
}

/* ========================================
   CHAT CONTAINER
   ======================================== */

.chat-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  max-width: 900px;
  margin: 0 auto;
  width: 100%;
  background: var(--bg-secondary);
  border-left: 1px solid var(--border-light);
  border-right: 1px solid var(--border-light);
}

.messages-list {
  flex: 1;
  overflow-y: auto;
  padding: var(--space-8);
  display: flex;
  flex-direction: column;
  gap: var(--space-6);
}

/* ========================================
   MESSAGE BUBBLES - Modern Cards
   ======================================== */

.message-wrapper {
  display: flex;
  max-width: 88%;
  animation: messageSlideIn 0.4s cubic-bezier(0.34, 1.56, 0.64, 1);
}

@keyframes messageSlideIn {
  from {
    opacity: 0;
    transform: translateY(20px) scale(0.95);
  }

  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.message-wrapper.user {
  align-self: flex-end;
}

.message-wrapper.assistant {
  align-self: flex-start;
}

.message-content {
  border-radius: var(--radius-xl);
  font-size: 0.9375rem;
  line-height: 1.7;
}

/* User Messages - Accent Gradient */
.user .message-content {
  background: linear-gradient(135deg, var(--primary-500), var(--primary-600));
  color: var(--text-primary);
  padding: var(--space-4) var(--space-6);
  border-bottom-right-radius: var(--radius-sm);
  box-shadow: var(--shadow-blue-glow);
}

/* Assistant Messages - Dark Card */
.assistant .message-content {
  background: var(--bg-tertiary);
  color: var(--text-primary);
  padding: var(--space-6);
  border: 1px solid var(--border-light);
  border-bottom-left-radius: var(--radius-sm);
  box-shadow: var(--shadow-md);
}

.assistant .message-content .text {
  font-size: 0.9375rem;
  line-height: 1.8;
  color: var(--text-primary);
}

/* Markdown formatting */
.assistant .message-content h2 {
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--accent-500);
  margin-bottom: var(--space-4);
  display: flex;
  align-items: center;
  gap: var(--space-3);
  font-family: var(--font-display);
}

.assistant .message-content h3 {
  font-size: 1rem;
  font-weight: 600;
  color: var(--primary-400);
  margin-top: var(--space-5);
  margin-bottom: var(--space-3);
}

.assistant .message-content p {
  margin-bottom: var(--space-4);
}

.assistant .message-content strong {
  font-weight: 600;
  color: var(--accent-400);
}

.message-meta {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: var(--space-3);
  margin-top: var(--space-4);
  font-size: 0.75rem;
  color: var(--text-tertiary);
}

.user .message-meta {
  color: rgba(255, 255, 255, 0.6);
}

.msg-action-btn {
  background: var(--bg-elevated);
  border: 1px solid var(--border-light);
  width: 32px;
  height: 32px;
  border-radius: var(--radius-full);
  font-size: 0.85rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all var(--transition-fast);
  color: var(--text-secondary);
}

.msg-action-btn:hover {
  background: var(--accent-500);
  color: var(--text-inverse);
  border-color: var(--accent-500);
  transform: scale(1.1);
}

/* Loading Indicator */
.loading-indicator {
  align-self: flex-start;
  background: var(--bg-tertiary);
  color: var(--text-secondary);
  padding: var(--space-4) var(--space-6);
  border-radius: var(--radius-xl);
  font-size: 0.875rem;
  display: flex;
  align-items: center;
  gap: var(--space-3);
  border: 1px solid var(--border-light);
}

.loading-indicator::before {
  content: '';
  width: 10px;
  height: 10px;
  background: var(--accent-500);
  border-radius: 50%;
  animation: pulse 1.2s infinite;
  box-shadow: var(--shadow-glow);
}

@keyframes pulse {

  0%,
  100% {
    opacity: 0.4;
    transform: scale(0.8);
  }

  50% {
    opacity: 1;
    transform: scale(1.2);
  }
}

/* ========================================
   CHAT INPUT - Sleek Modern
   ======================================== */

.chat-input-area {
  padding: var(--space-6);
  background: var(--bg-secondary);
  border-top: 1px solid var(--border-light);
}

.input-box {
  display: flex;
  align-items: center;
  background: var(--bg-tertiary);
  border: 2px solid var(--border-light);
  border-radius: var(--radius-2xl);
  padding: var(--space-3) var(--space-5);
  gap: var(--space-3);
  transition: all var(--transition-fast);
}

.input-box:focus-within {
  border-color: var(--primary-500);
  background: var(--bg-elevated);
  box-shadow: var(--shadow-blue-glow);
}

.input-box input {
  flex: 1;
  background: none;
  border: none;
  font-size: 1rem;
  color: var(--text-primary);
  outline: none;
  font-family: var(--font-body);
}

.input-box input::placeholder {
  color: var(--text-tertiary);
}

.voice-btn,
.send-btn {
  background: none;
  border: none;
  width: 44px;
  height: 44px;
  border-radius: var(--radius-full);
  font-size: 1.3rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all var(--transition-fast);
  color: var(--text-secondary);
}

.voice-btn:hover,
.send-btn:hover:not(:disabled) {
  background: var(--accent-500);
  color: var(--text-inverse);
  transform: scale(1.1);
}

.voice-btn.recording {
  background: var(--danger-500);
  color: var(--text-primary);
  animation: recordPulse 1.5s ease-in-out infinite;
}

@keyframes recordPulse {

  0%,
  100% {
    box-shadow: 0 0 0 0 rgba(239, 68, 68, 0.6);
  }

  50% {
    box-shadow: 0 0 0 12px rgba(239, 68, 68, 0);
  }
}

.send-btn {
  background: var(--accent-500);
  color: var(--text-inverse);
}

.send-btn:hover:not(:disabled) {
  background: var(--accent-600);
  box-shadow: var(--shadow-glow);
}

.send-btn:disabled {
  background: var(--gray-700);
  color: var(--gray-500);
  cursor: not-allowed;
  transform: none;
}

/* ========================================
   CONTEXT CARDS - Bold Accent Borders
   ======================================== */

.context-card {
  background: var(--bg-elevated);
  border-radius: var(--radius-lg);
  padding: var(--space-5);
  margin-top: var(--space-5);
  border-left: 4px solid var(--accent-500);
  transition: all var(--transition-normal);
  border: 1px solid var(--border-light);
  border-left: 4px solid var(--accent-500);
}

.context-card:hover {
  box-shadow: var(--shadow-lg);
  transform: translateY(-3px);
  border-color: var(--accent-500);
}

.context-card.border-accent-red {
  border-left-color: var(--danger-500);
}

.context-card.border-accent-orange {
  border-left-color: var(--warning-500);
}

.card-header {
  display: flex;
  align-items: center;
  gap: var(--space-4);
  margin-bottom: var(--space-4);
}

.card-icon {
  font-size: 1.4rem;
  width: 44px;
  height: 44px;
  background: var(--primary-500);
  border-radius: var(--radius-lg);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-primary);
  box-shadow: var(--shadow-blue-glow);
}

.card-title {
  font-size: 1rem;
  font-weight: 700;
  color: var(--text-primary);
  margin: 0;
  font-family: var(--font-display);
}

.card-body {
  font-size: 0.9rem;
  line-height: 1.7;
  color: var(--text-secondary);
}

.card-actions {
  display: flex;
  gap: var(--space-3);
  margin-top: var(--space-5);
  padding-top: var(--space-4);
  border-top: 1px solid var(--border-light);
}

.card-actions button {
  flex: 1;
  padding: var(--space-3) var(--space-4);
  font-size: 0.875rem;
  font-weight: 600;
  border-radius: var(--radius-lg);
  cursor: pointer;
  transition: all var(--transition-fast);
  background: var(--accent-500);
  color: var(--text-inverse);
  border: none;
  font-family: var(--font-display);
}

.card-actions button:hover {
  background: var(--accent-600);
  box-shadow: var(--shadow-glow);
  transform: translateY(-2px);
}

.card-actions button.secondary {
  background: transparent;
  color: var(--text-secondary);
  border: 1px solid var(--border-medium);
}

.card-actions button.secondary:hover {
  background: var(--bg-tertiary);
  color: var(--text-primary);
  border-color: var(--text-secondary);
}

/* ========================================
   QUICK ACTIONS - Pill Buttons
   ======================================== */

.quick-actions {
  display: flex;
  gap: var(--space-3);
  flex-wrap: wrap;
  margin-top: var(--space-5);
}

.action-btn {
  display: inline-flex;
  align-items: center;
  gap: var(--space-2);
  padding: var(--space-3) var(--space-5);
  font-size: 0.875rem;
  font-weight: 600;
  font-family: var(--font-display);
  border-radius: var(--radius-full);
  cursor: pointer;
  transition: all var(--transition-fast);
  white-space: nowrap;
}

.action-btn.primary {
  background: var(--primary-500);
  color: var(--text-primary);
  border: none;
}

.action-btn.primary:hover {
  background: var(--primary-400);
  transform: translateY(-2px);
  box-shadow: var(--shadow-blue-glow);
}

.action-btn.outline {
  background: transparent;
  color: var(--text-secondary);
  border: 1px solid var(--border-medium);
}

.action-btn.outline:hover {
  background: var(--accent-500);
  color: var(--text-inverse);
  border-color: var(--accent-500);
}

/* ========================================
   LOCAL MOVE FOOTER - Highlight Section
   ======================================== */

.local-move-footer {
  background: linear-gradient(135deg, var(--bg-elevated), var(--bg-tertiary));
  padding: var(--space-4) var(--space-5);
  border-radius: var(--radius-lg);
  margin-top: var(--space-4);
  font-size: 0.875rem;
  color: var(--accent-400);
  display: flex;
  align-items: flex-start;
  gap: var(--space-3);
  border: 1px solid var(--accent-500);
  box-shadow: var(--shadow-glow);
}

.local-move-footer strong {
  font-weight: 700;
  white-space: nowrap;
  color: var(--accent-500);
}

/* ========================================
   PRICE RATING
   ======================================== */

.price-rating {
  display: flex;
  align-items: center;
  gap: var(--space-4);
  font-size: 1rem;
  font-weight: 700;
  color: var(--accent-500);
  margin-bottom: var(--space-3);
  font-family: var(--font-display);
}

.price-rating .divider {
  color: var(--border-medium);
}

/* ========================================
   TIMING BLOCKS
   ======================================== */

.timing-details .timeline {
  display: flex;
  flex-direction: column;
  gap: var(--space-3);
}

.time-block {
  display: flex;
  align-items: center;
  gap: var(--space-4);
  padding: var(--space-3) var(--space-4);
  border-radius: var(--radius-lg);
  font-size: 0.875rem;
  border: 1px solid var(--border-light);
}

.time-block.best {
  background: rgba(16, 185, 129, 0.15);
  border-color: var(--success-500);
}

.time-block.okay {
  background: rgba(245, 158, 11, 0.15);
  border-color: var(--warning-500);
}

.time-block.avoid {
  background: rgba(239, 68, 68, 0.15);
  border-color: var(--danger-500);
}

.time-block .status {
  font-weight: 700;
  font-family: var(--font-display);
}

.time-block .range {
  font-weight: 600;
  color: var(--text-primary);
}

.time-block .label {
  color: var(--text-tertiary);
  font-size: 0.8rem;
}

/* ========================================
   SAFETY DETAILS
   ======================================== */

.safety-details .safety-status.urgent {
  background: rgba(239, 68, 68, 0.2);
  color: var(--danger-500);
  padding: var(--space-3) var(--space-4);
  border-radius: var(--radius-lg);
  font-weight: 600;
  font-size: 0.875rem;
  margin-bottom: var(--space-4);
  border: 1px solid var(--danger-500);
}

.moves-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: var(--space-4);
}

.move-col {
  padding: var(--space-4);
  border-radius: var(--radius-lg);
  font-size: 0.875rem;
  border: 1px solid var(--border-light);
}

.move-col.safe {
  background: rgba(16, 185, 129, 0.1);
  border-color: var(--success-500);
}

.move-col.risk {
  background: rgba(239, 68, 68, 0.1);
  border-color: var(--danger-500);
}

.move-col strong {
  display: block;
  margin-bottom: var(--space-3);
  font-size: 0.8rem;
  font-weight: 700;
  font-family: var(--font-display);
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.move-col ul {
  list-style: none;
  padding: 0;
}

.move-col ul li {
  padding: var(--space-1) 0;
  color: var(--text-secondary);
}

/* ========================================
   PERSONALITY PANEL
   ======================================== */

.personality-panel {
  width: 340px;
  background: var(--bg-secondary);
  border-left: 1px solid var(--border-light);
  padding: var(--space-8);
  overflow-y: auto;
  display: none;
}

.personality-panel.open {
  display: block;
}

/* ========================================
   MOBILE RESPONSIVE
   ======================================== */

@media (max-width: 768px) {
  .app-header {
    padding: 0 var(--space-4);
    height: 64px;
  }

  .header-nav {
    display: none;
  }

  .chat-container {
    max-width: 100%;
    border: none;
  }

  .messages-list {
    padding: var(--space-4);
    gap: var(--space-4);
  }

  .message-wrapper {
    max-width: 95%;
  }

  .personality-panel {
    position: fixed;
    top: 64px;
    right: 0;
    bottom: 0;
    width: 100%;
    max-width: 340px;
    z-index: 200;
    transform: translateX(100%);
    transition: transform var(--transition-normal);
  }

  .personality-panel.open {
    transform: translateX(0);
  }
}

/* ========================================
   FOOD DETAILS
   ======================================== */

.food-details .description {
  color: var(--text-secondary);
  font-size: 0.9rem;
  line-height: 1.7;
}

/* ========================================
   CHIPS WRAPPER
   ======================================== */

.chips-wrapper {
  align-self: flex-start;
  max-width: 90%;
  margin-left: 48px;
}