@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@400;500;600;700;800&family=Inter:wght@400;500;600;700&display=swap');

:root {
    /* ========================================
       BEACHHACK INSPIRED COLOR PALETTE
       Bold, Modern, High-Contrast Design
       ======================================== */

    /* Primary - Royal Blue */
    --primary-500: #4361EE;
    --primary-600: #3651D4;
    --primary-700: #2A41BA;
    --primary-400: #5A7AFF;
    --primary-100: #E8EDFF;

    /* Accent - Neon Lime (High Impact) */
    --accent-500: #D4FF00;
    --accent-400: #E2FF4D;
    --accent-600: #B8E600;
    --accent-100: #F5FFB3;

    /* Secondary - Vibrant Coral */
    --secondary-500: #FF6B6B;
    --secondary-400: #FF8A8A;
    --secondary-100: #FFE5E5;

    /* Semantic Colors */
    --success-500: #10B981;
    --success-100: #D1FAE5;
    --warning-500: #F59E0B;
    --warning-100: #FEF3C7;
    --danger-500: #EF4444;
    --danger-100: #FEE2E2;

    /* Dark Mode First - Modern Aesthetic */
    --gray-50: #FAFAFA;
    --gray-100: #F4F4F5;
    --gray-200: #E4E4E7;
    --gray-300: #D4D4D8;
    --gray-400: #A1A1AA;
    --gray-500: #71717A;
    --gray-600: #52525B;
    --gray-700: #3F3F46;
    --gray-800: #27272A;
    --gray-900: #18181B;
    --gray-950: #0A0A0A;

    /* Backgrounds - Dark Theme */
    --bg-primary: #0A0A0A;
    --bg-secondary: #121212;
    --bg-tertiary: #1A1A1A;
    --bg-elevated: #242424;

    /* Text Colors */
    --text-primary: #FFFFFF;
    --text-secondary: #A1A1AA;
    --text-tertiary: #71717A;
    --text-inverse: #0A0A0A;

    /* Borders */
    --border-light: #27272A;
    --border-medium: #3F3F46;
    --border-accent: #4361EE;

    /* Shadows */
    --shadow-xs: 0 1px 2px rgba(0, 0, 0, 0.3);
    --shadow-sm: 0 2px 4px rgba(0, 0, 0, 0.4);
    --shadow-md: 0 4px 12px rgba(0, 0, 0, 0.5);
    --shadow-lg: 0 8px 24px rgba(0, 0, 0, 0.6);
    --shadow-xl: 0 16px 48px rgba(0, 0, 0, 0.7);
    --shadow-glow: 0 0 20px rgba(212, 255, 0, 0.3);
    --shadow-blue-glow: 0 0 30px rgba(67, 97, 238, 0.4);

    /* Spacing Scale */
    --space-1: 4px;
    --space-2: 8px;
    --space-3: 12px;
    --space-4: 16px;
    --space-5: 20px;
    --space-6: 24px;
    --space-8: 32px;
    --space-10: 40px;
    --space-12: 48px;
    --space-16: 64px;

    /* Border Radius */
    --radius-sm: 6px;
    --radius-md: 10px;
    --radius-lg: 14px;
    --radius-xl: 20px;
    --radius-2xl: 28px;
    --radius-full: 9999px;

    /* Typography */
    --font-display: 'Space Grotesk', sans-serif;
    --font-body: 'Inter', sans-serif;

    /* Transitions */
    --transition-fast: 150ms cubic-bezier(0.4, 0, 0.2, 1);
    --transition-normal: 250ms cubic-bezier(0.4, 0, 0.2, 1);
    --transition-slow: 400ms cubic-bezier(0.4, 0, 0.2, 1);
    --transition-bounce: 500ms cubic-bezier(0.34, 1.56, 0.64, 1);
}

/* ========================================
   GLOBAL STYLES
   ======================================== */

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html {
    scroll-behavior: smooth;
}

body {
    font-family: var(--font-body);
    font-size: 15px;
    line-height: 1.6;
    color: var(--text-primary);
    background: var(--bg-primary);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

h1, h2, h3, h4, h5, h6 {
    font-family: var(--font-display);
    font-weight: 700;
    line-height: 1.2;
    color: var(--text-primary);
    letter-spacing: -0.02em;
}

/* ========================================
   SCROLLBAR - Sleek Modern Style
   ======================================== */

::-webkit-scrollbar {
    width: 8px;
    height: 8px;
}

::-webkit-scrollbar-track {
    background: var(--bg-secondary);
}

::-webkit-scrollbar-thumb {
    background: var(--gray-700);
    border-radius: var(--radius-full);
}

::-webkit-scrollbar-thumb:hover {
    background: var(--primary-500);
}

/* ========================================
   SELECTION
   ======================================== */

::selection {
    background: var(--accent-500);
    color: var(--text-inverse);
}

/* ========================================
   UTILITY CLASSES
   ======================================== */

.text-gradient {
    background: linear-gradient(135deg, var(--accent-500), var(--primary-400));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.glow-accent {
    box-shadow: var(--shadow-glow);
}

.glow-blue {
    box-shadow: var(--shadow-blue-glow);
}