/* ========================================
   TOPIC CHIPS STYLES
   Clean, tappable category selection
   ======================================== */

.topic-chips-container {
    padding: var(--space-4);
    background: var(--bg-tertiary);
    border-radius: var(--radius-lg);
    margin-top: var(--space-4);
}

.chips-label {
    font-size: 0.75rem;
    font-weight: 600;
    color: var(--text-tertiary);
    text-transform: uppercase;
    letter-spacing: 0.5px;
    margin: 0 0 var(--space-3) 0;
}

.topic-chips {
    display: flex;
    gap: var(--space-2);
    flex-wrap: wrap;
}

.topic-chip {
    display: flex;
    align-items: center;
    gap: var(--space-2);
    padding: var(--space-2) var(--space-4);
    border-radius: var(--radius-full);
    border: none;
    font-size: 0.875rem;
    font-weight: 500;
    font-family: var(--font-body);
    cursor: pointer;
    transition: all var(--transition-fast);
    box-shadow: var(--shadow-xs);
}

.topic-chip .chip-icon {
    font-size: 1rem;
}

.topic-chip .chip-label {
    font-weight: 500;
}

/* Color Variants */
.topic-chip.teal {
    background: var(--primary-100);
    color: var(--primary-700);
}

.topic-chip.teal:hover {
    background: var(--primary-500);
    color: white;
    box-shadow: var(--shadow-md);
    transform: translateY(-2px);
}

.topic-chip.coral {
    background: var(--secondary-100);
    color: #9A3412;
}

.topic-chip.coral:hover {
    background: var(--secondary-500);
    color: white;
    box-shadow: var(--shadow-md);
    transform: translateY(-2px);
}

.topic-chip.gold {
    background: var(--accent-100);
    color: #854D0E;
}

.topic-chip.gold:hover {
    background: var(--accent-500);
    color: white;
    box-shadow: var(--shadow-md);
    transform: translateY(-2px);
}

.topic-chip.purple {
    background: #F3E8FF;
    color: #6B21A8;
}

.topic-chip.purple:hover {
    background: #9333EA;
    color: white;
    box-shadow: var(--shadow-md);
    transform: translateY(-2px);
}

/* Example Questions */
.example-questions {
    margin-top: var(--space-4);
    padding-top: var(--space-3);
    border-top: 1px solid var(--border-light);
}

.examples-label {
    font-size: 0.75rem;
    font-weight: 500;
    color: var(--text-tertiary);
    margin: 0 0 var(--space-2) 0;
}

.examples-list {
    display: flex;
    flex-direction: column;
    gap: var(--space-2);
}

.examples-list button {
    background: var(--bg-primary);
    border: 1px solid var(--border-light);
    border-radius: var(--radius-md);
    padding: var(--space-3);
    font-size: 0.8125rem;
    color: var(--text-secondary);
    text-align: left;
    cursor: pointer;
    transition: all var(--transition-fast);
    font-style: italic;
    font-family: var(--font-body);
}

.examples-list button:hover {
    background: var(--primary-100);
    border-color: var(--primary-500);
    color: var(--primary-700);
}

/* ========================================
   RESPONSIVE
   ======================================== */

@media (max-width: 480px) {
    .topic-chips {
        gap: var(--space-1);
    }

    .topic-chip {
        padding: var(--space-2) var(--space-3);
        font-size: 0.8125rem;
    }
}