/* VoiceVisualizer Styles - Siri-like Voice Animation */

.voice-overlay {
    position: fixed;
    inset: 0;
    background: linear-gradient(180deg,
            rgba(10, 15, 20, 0.98) 0%,
            rgba(15, 25, 30, 0.98) 100%);
    z-index: 9999;
    display: flex;
    align-items: center;
    justify-content: center;
    backdrop-filter: blur(20px);
    animation: fadeIn 0.3s ease-out;
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

.visualizer-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 24px;
    padding: 32px;
    max-width: 400px;
    width: 100%;
}

.voice-canvas {
    border-radius: 50%;
    animation: pulseGlow 2s ease-in-out infinite;
}

@keyframes pulseGlow {

    0%,
    100% {
        filter: drop-shadow(0 0 20px rgba(32, 180, 180, 0.3));
    }

    50% {
        filter: drop-shadow(0 0 40px rgba(32, 200, 200, 0.5));
    }
}

.voice-status {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 8px;
}

.recording-badge {
    display: flex;
    align-items: center;
    gap: 8px;
    background: rgba(255, 80, 80, 0.15);
    border: 1px solid rgba(255, 80, 80, 0.3);
    padding: 8px 16px;
    border-radius: 20px;
    color: #ff6b6b;
    font-size: 0.8rem;
    font-weight: 700;
    letter-spacing: 2px;
    text-transform: uppercase;
}

.rec-dot {
    width: 8px;
    height: 8px;
    background: #ff6b6b;
    border-radius: 50%;
    animation: recPulse 1s ease-in-out infinite;
}

@keyframes recPulse {

    0%,
    100% {
        opacity: 1;
        transform: scale(1);
    }

    50% {
        opacity: 0.5;
        transform: scale(0.8);
    }
}

.duration {
    font-size: 2rem;
    font-weight: 300;
    color: rgba(255, 255, 255, 0.8);
    font-family: 'SF Mono', 'Monaco', monospace;
}

.speaking-text {
    color: rgba(100, 220, 220, 0.9);
    font-size: 1.1rem;
    font-weight: 500;
    animation: speakingPulse 1.5s ease-in-out infinite;
}

@keyframes speakingPulse {

    0%,
    100% {
        opacity: 0.8;
    }

    50% {
        opacity: 1;
    }
}

.transcript-container {
    min-height: 80px;
    max-height: 150px;
    width: 100%;
    overflow-y: auto;
    text-align: center;
    padding: 16px;
    background: rgba(255, 255, 255, 0.03);
    border-radius: 16px;
    border: 1px solid rgba(255, 255, 255, 0.05);
}

.final-transcript {
    color: rgba(255, 255, 255, 0.95);
    font-size: 1.1rem;
    line-height: 1.6;
    margin: 0;
    font-weight: 500;
}

.interim-transcript {
    color: rgba(100, 200, 200, 0.7);
    font-size: 1rem;
    line-height: 1.5;
    margin: 8px 0 0 0;
    font-style: italic;
}

.placeholder-text {
    color: rgba(255, 255, 255, 0.4);
    font-size: 1rem;
    font-style: italic;
    margin: 0;
}

.stop-btn {
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 14px 32px;
    background: linear-gradient(135deg, rgba(255, 80, 80, 0.2), rgba(255, 100, 100, 0.1));
    border: 1px solid rgba(255, 80, 80, 0.4);
    border-radius: 30px;
    color: #ff8a8a;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
}

.stop-btn:hover {
    background: linear-gradient(135deg, rgba(255, 80, 80, 0.3), rgba(255, 100, 100, 0.2));
    border-color: rgba(255, 80, 80, 0.6);
    transform: scale(1.05);
}

.stop-icon {
    font-size: 0.8rem;
}

.hint-text {
    color: rgba(255, 255, 255, 0.4);
    font-size: 0.85rem;
    margin: 0;
    text-align: center;
}

/* Voice Button Recording State */
.voice-btn.recording {
    background: rgba(255, 80, 80, 0.2) !important;
    animation: recordingPulse 1s ease-in-out infinite;
}

@keyframes recordingPulse {

    0%,
    100% {
        box-shadow: 0 0 0 0 rgba(255, 80, 80, 0.4);
    }

    50% {
        box-shadow: 0 0 0 10px rgba(255, 80, 80, 0);
    }
}

/* Mobile Optimizations */
@media (max-width: 480px) {
    .visualizer-container {
        padding: 24px 16px;
    }

    .voice-canvas {
        width: 250px;
        height: 250px;
    }

    .duration {
        font-size: 1.5rem;
    }

    .transcript-container {
        min-height: 60px;
        max-height: 100px;
    }

    .final-transcript {
        font-size: 1rem;
    }
}

/* Scrollbar styling for transcript */
.transcript-container::-webkit-scrollbar {
    width: 4px;
}

.transcript-container::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.05);
    border-radius: 2px;
}

.transcript-container::-webkit-scrollbar-thumb {
    background: rgba(100, 200, 200, 0.3);
    border-radius: 2px;
}

.transcript-container::-webkit-scrollbar-thumb:hover {
    background: rgba(100, 200, 200, 0.5);
}